import React, {Component} from "react";
import MultiCartItemTooltip from "./MultiCartItemTooltip";
import OnlineUtils from "eshop/utils/OnlineUtils";

export default class MultiCartMnpBenefitComponent extends Component {
    constructor (props) {
        super(props);
    }

    getMnpBenefitData(processType, mnpData, monthlyPrices) {
        if (!OnlineUtils.isMnp(processType))
            return false;

        if (monthlyPrices && (monthlyPrices[0].originalGrossPrice == 0 || monthlyPrices[0].price===0) && monthlyPrices[0].monthTo) {
            return monthlyPrices[0].monthTo;
        }
    }

    render() {

        let months = this.getMnpBenefitData(this.props.processType, this.props.mnpData, this.props.monthlyPrices),
            formattedMonth = months + ((months < 5) ? ' miesiące' : ' miesięcy');

        return months ? (
            <div className="u-padding-top u-font-bold u-padding u-padding-left-l u-padding-right-l g-yellowf-bg u-spacing-l">
                Dla przenoszących numer {formattedMonth} za darmo!
                {this.props.tooltip &&
                <MultiCartItemTooltip>
                    {this.props.tooltip}
                </MultiCartItemTooltip>
                }
            </div>
        ):(null)
    }


}